.news { position: relative; top: -245px; margin-bottom: -157px; }
.news__bl { padding: 83px 100px 58px; color: #312e31; background: #FFF; border: 2px solid #3096a8; }
.news__el { margin-bottom: 30px; }
.news__ttl { font-size: 24px; color: #3096a8; font-family: "helvetica-medium", Helvetica, sans-serif; font-weight: normal; }
.news__details { color: #312e31; font-family: "helvetica-medium", Helvetica, sans-serif; font-weight: normal; }
.news__details:hover { text-decoration: none; }
.news .h2 { margin-bottom: 23px; padding-left: 100px; color: #312e31; }
@media only screen and (max-width: 920px) { .news .h2 { padding-left: 0; }
  .news__bl { padding: 40px 20px; } }

.more { position: absolute; height: 71px; left: 100px; right: 100px; bottom: -35.5px; text-align: right; overflow: hidden; }
.more:before { position: absolute; top: 0; bottom: 0; right: 0; background: #fff; content: ""; width: 204px; border: 2px solid #3096a8; transition: 0.5s; }
.more.is-active:before { width: 100%; }
.more.is-active .pagination { position: relative; visibility: visible; opacity: 1; }
.more__in { position: relative; z-index: 1; }
.more .btn { padding: 19px 35px 21px; margin: 2px 2px 2px 0; text-decoration: none; background: #fff; color: #3096a8; transition: opacity 0.5s, visibility 0.5s; }
.more .btn.is-hidden { position: absolute; opacity: 0; visibility: hidden; }
.more .btn:hover { background: #FF7400; color: #FFF; }
@media only screen and (max-width: 920px) { .more { left: 0; right: 0; } }
@media only screen and (max-width: 690px) { .more { height: 61px; }
  .more:before { width: 173px; }
  .more .btn { padding: 19px 35px 17px; } }

.pagination { position: absolute; text-align: center; font-size: 0; color: #3096a8; line-height: 71px; opacity: 0; visibility: hidden; transition: opacity 0.6s, visibility 0.6s; font-family: "helvetica-medium", Helvetica, sans-serif; font-weight: normal; }
.pagination__next, .pagination__prev { display: inline-block; vertical-align: middle; transition: border-color 0.5s; }
.pagination__prev { margin-right: 17px; width: 0px; height: 0px; border-style: solid; border-width: 8.5px 15px 8.5px 0; border-color: transparent #FF7400 transparent transparent; }
.pagination__prev:hover { border-color: transparent #3096a8 transparent transparent; }
.pagination__next { margin-left: 17px; width: 0px; height: 0px; border-style: solid; border-width: 8.5px 0 8.5px 15px; border-color: transparent transparent transparent #FF7400; }
.pagination__next:hover { border-color: transparent transparent transparent #3096a8; }
.pagination__dots { letter-spacing: 0.35em; }
.pagination__list { display: inline-block; font-size: 24px; vertical-align: middle; }
.pagination__list li { display: inline-block; vertical-align: middle; line-height: 1; }
.pagination__list a { text-decoration: none; color: #3096a8; transition: color 0.2s; }
.pagination__list a:hover { color: #FF7400; }
.pagination__list a.is-active { color: #FF7400; text-decoration: underline; }
@media only screen and (max-width: 690px) { .pagination { line-height: 61px; }
  .pagination__list { font-size: 20px; } }

.contacts { padding-bottom: 48px; color: #fff; }
