.contacts .col.-sm { text-transform: uppercase; }
.contacts .col.-sm .h2 { text-transform: none; color: #86e9fa; }
.contacts .h2 { margin-bottom: 35px; }
@media only screen and (max-width: 1020px) { .contacts__num { font-size: 30px; } }

.location { background: #FFF; }
.location__logo { padding: 31px 20px; text-align: center; background: #FFF; }

